  width: 100%;
  max-width: 140rem;
  margin: 0 auto;
  padding: 0 1.6rem;
}

.grid {
  gap: 9.5rem 6.4rem;
  display: grid;
}

.grid:not(:last-child) {
  margin-bottom: 4.8rem;
}

.grid--2-cols {
  grid-template-columns: repeat(2, 1fr);
}

.grid--3-cols {
  grid-template-columns: repeat(3, 1fr);
}

